<template>
  <div class="home-page">
    <div class="content-header">
      <!-- <bread-crumb current-level="首页" /> -->
      <div class="header-text">数据更新至</div>
      <div class="latest-time">{{ baseInfo.date || '-' }}</div>
    </div>
    <div class="statistics-info">
      <gs-loading :is-show="isBaseInfoLoading" />
      <div
        v-for="(item, index) in baseInfoArr"
        :key="index"
        class="sum-block"
      >
        <div class="info-icon">
          <i :class="`iconfont icon-${item.icon}`" />
        </div>
        <div class="info-num">
          {{ item.value }}
        </div>
        <div class="info-text">
          {{ item.label }}
        </div>
      </div>
    </div>
    <div class="database-table-chart">
      <div class="database-chart">
        <div class="chart-title">
          最近30天数据库数量变化
        </div>
        <div class="chart-block">
          <gs-loading :is-show="dbChartLoading" />
          <home-chart
            :options="dbChartOption"
            type="bar"
          />
        </div>
      </div>
      <div class="table-chart">
        <div class="chart-title">
          最近30天数据表数量趋势
        </div>
        <div class="chart-block">
          <gs-loading :is-show="tbChartLoading" />
          <home-chart :options="tbChartOption" />
        </div>
      </div>
    </div>
    <div class="system-and-table">
      <div class="system-item">
        <div class="block-title">贴源层子系统基本情况</div>
        <system-table />
      </div>
      <div class="table-item">
        <div class="block-title">
          数据表使用次数排名
          <gs-tooltip placement="top">
            <i class="iconfont icon-wenhao" />
            <div slot="template">
              <div>使用次数：</div>
              <div style="text-indent: 2em;">使用了该数据表的业务对象的数量</div>
              <div>业务占比：</div>
              <div style="text-indent: 2em;">该数据表的使用次数 / 所有业务对象数量总和</div>
            </div>
          </gs-tooltip>
        </div>
        <data-table
          :loading="isDataTableLoading"
          :table-props="tableProps"
          :pagination-props="paginationProps"
          @page-change="handlePageChange"
          @page-size-change="handlePageSizeChange"
        >
          <el-table-column
            label="序号"
            align="center"
            width="60px"
          >
            <template slot-scope="scope">
              <img
                v-if="scope.$index === 0 && page === 1"
                :src="firstRank"
                class="rank-img"
              >
              <img
                v-else-if="scope.$index === 1 && page === 1"
                :src="secondRank"
                class="rank-img"
              >
              <img
                v-else-if="scope.$index === 2 && page === 1"
                :src="thirdRank"
                class="rank-img"
              >
              <span v-else>{{ scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="数据表名称"
            prop="tableName"
          />
          <el-table-column
            label="使用次数"
            prop="times"
          />
          <el-table-column
            label="业务占比"
            prop="proportion"
          >
            <template slot-scope="scope">
              <gs-progress :percent="formatPercent(scope.row.proportion)">
                {{ formatPercent(scope.row.proportion) }}%
              </gs-progress>
            </template>
          </el-table-column>
        </data-table>
      </div>
    </div>
    <div class="tech-business">
      <div class="proportion-info">
        <div class="chart-title">已补充技术信息的业务对象占比</div>
        <div class="chart-block">
          <gs-loading :is-show="techBusinessLoading" />
          <home-chart
            :options="techBusinessOption"
            type="pie"
          />
        </div>
      </div>
      <div class="tendency-info">
        <div class="chart-title">最近30天业务对象总量趋势</div>
        <div class="chart-block">
          <gs-loading :is-show="totalBusinessChartLoading" />
          <home-chart :options="totalBusinessChartOption" />
        </div>
      </div>
    </div>
    <role-table />
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import HomeChart from 'src/components/chart';
import BreadCrumb from 'src/components/breadcrumb';
import SystemTable from './system-table';
import RoleTable from './role-table';
import ChartMixin from './chart-mixin';
import {
  checkLoading
} from 'src/store/status';
import './style';

export default {
  components: {
    DataTable,
    HomeChart,
    BreadCrumb,
    SystemTable,
    RoleTable
  },

  mixins: [ChartMixin],

  data() {
    return {
      page: 1,
      pageSize: 10,
      firstRank: require('../../../public/images/rank-first.png'),
      secondRank: require('../../../public/images/rank-second.png'),
      thirdRank: require('../../../public/images/rank-third.png')
    };
  },

  computed: {
    ...mapState('home', {
      tableUsage: 'tableUsage',
      baseInfo: 'baseInfo',
      baseInfoStatus: 'baseInfoStatus'
    }),

    tableProps() {
      const {
        tableUsage
      } = this;

      return {
        total: tableUsage.total,
        data: tableUsage.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total,
        layout: 'range,total,pager,jumper,jumpbtn'
      };
    },
    isDataTableLoading() {
      return checkLoading(this.tableUsage.status);
    },
    isBaseInfoLoading() {
      return checkLoading(this.baseInfoStatus);
    },
    baseInfoArr() {
      const {
        baseInfo
      } = this;
      const {
        databaseTotal = 0,
        tableTotal = 0,
        subsystemTotal = 0,
        sourceLogicDBTotal = 0,
        sourceEntityDBTotal = 0
      } = baseInfo;
      return [
        {
          icon: 'shujuku',
          value: databaseTotal,
          label: '数据库数量'
        },
        {
          icon: 'biaoge',
          value: tableTotal,
          label: '数据表数量'
        },
        {
          icon: 'xitong',
          value: subsystemTotal,
          label: '贴源层子系统数量'
        },
        {
          icon: 'luojiti',
          value: sourceLogicDBTotal,
          label: '贴源层逻辑库数量'
        },
        {
          icon: 'xiangmumingchengshiti',
          value: sourceEntityDBTotal,
          label: '贴源层实体库数量'
        }
      ];
    }
  },

  watch: {
  },

  mounted() {
    this.getBaseInfo();
    this.queryTableUsage();
  },

  methods: {
    ...mapActions('home', [
      'getBaseInfo',
      'getTableUsage'
    ]),

    queryTableUsage() {
      const {
        page,
        pageSize
      } = this;
      this.getTableUsage({
        offset: (page - 1) * pageSize,
        limit: pageSize
      });
    },

    formatPercent(percent) {
      const num = parseFloat(percent) * 100;
      return parseFloat(num.toFixed(2));
    },

    handlePageChange(page) {
      this.page = page;
      this.queryTableUsage();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.queryTableUsage();
    }
  }
};
</script>




