<template>
  <v-chart
    ref="chart"
    :auto-resize="true"
    :options="chartOptions"
  />
</template>

<script>
import ECharts from 'vue-echarts';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/lines';
import 'echarts/lib/component/polar';
import 'echarts/lib/component/title';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/legend';

export default {
  name: 'HomeChart',
  components: {
    'v-chart': ECharts
  },
  props: {
    options: {
      type: Object,
      default: () => {
        return {};
      }
    },
    type: {
      type: String,
      default: 'line'
    }
  },

  data() {
    return {
      defaultBarOptions: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        xAxis: {
          type: 'category',
          data: []
        },
        yAxis: {
          type: 'value'
        }
      },
      defaultLineOptions: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value'
        }
      },
      defaultPieOptions: {
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        }
      }
    };
  },

  computed: {
    defaultOptions() {
      const {
        type
      } = this;
      let option = {};
      switch (type) {
        case 'bar':
          option = this.defaultBarOptions;
          break;
        case 'line':
          option = this.defaultLineOptions;
          break;
        case 'pie':
          option = this.defaultPieOptions;
          break;
      }
      return option;
    },
    chartOptions() {
      const {
        options
      } = this;
      return {
        ...this.defaultOptions,
        ...options
      };
    }
  },

  watch: {
  },

  mounted() {
  },

  methods: {
    resize() {
      let instance = this.$refs.chart;
      instance.resize();
    }
  }
};
</script>



