<template>
  <div class="role-table">
    <div class="block-title">
      系统角色与用户
    </div>
    <div class="table-block">
      <div class="role-item">
        <data-table
          :loading="isRoleTableLoading"
          :table-props="roleTableProps"
          :pagination-props="rolePaginationProps"
          @page-change="handleRolePageChange"
          @page-size-change="handleRolePageSizeChange"
        >
          <el-table-column
            label="序号"
            align="center"
            width="60px"
          >
            <template slot-scope="scope">
              <img
                v-if="scope.$index === 0 && rolePage === 1"
                :src="firstRank"
                class="rank-img"
              >
              <img
                v-else-if="scope.$index === 1 && rolePage === 1"
                :src="secondRank"
                class="rank-img"
              >
              <img
                v-else-if="scope.$index === 2 && rolePage === 1"
                :src="thirdRank"
                class="rank-img"
              >
              <span v-else>{{ scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="角色名称"
            prop="roleName"
            show-overflow-tooltip
          />
          <el-table-column
            label="用户数"
            prop="number"
            width="400px"
          >
            <template slot-scope="scope">
              <div class="user-count">
                <span class="count-number">{{ scope.row.number }}</span>
                <span
                  :style="`width: ${scope.row.proportion * 300}px`"
                  class="count-bar"
                />
              </div>
            </template>
          </el-table-column>
        </data-table>
      </div>
      <div class="user-item">
        <gs-select
          v-model="role"
          :loading="isRoleSelectLoading"
          searchable
        >
          <gs-option
            v-for="item in roleList.list"
            :key="item.roleId"
            :label="item.roleName"
            :value="item.roleId"
          />
        </gs-select>
        <data-table
          :loading="isUserTableLoading"
          :table-props="userTableProps"
          :pagination-props="userPaginationProps"
          @page-change="handleUserPageChange"
          @page-size-change="handleUserPageSizeChange"
        >
          <el-table-column
            label="序号"
            type="index"
          />
          <el-table-column
            label="账号"
            prop="account"
          />
          <el-table-column
            label="姓名"
            prop="username"
          />
        </data-table>
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import {
  checkLoading
} from 'src/store/status';

export default {
  components: {
    DataTable
  },

  data() {
    return {
      role: '',
      rolePage: 1,
      rolePageSize: 10,
      userPage: 1,
      userPageSize: 10,
      firstRank: require('../../../public/images/rank-first.png'),
      secondRank: require('../../../public/images/rank-second.png'),
      thirdRank: require('../../../public/images/rank-third.png')
    };
  },

  computed: {
    ...mapState('home', {
      roleList: 'roleList',
      roles: 'roles',
      users: 'users'
    }),

    isRoleSelectLoading() {
      return checkLoading(this.roleList.status);
    },
    roleTableProps() {
      const {
        roles
      } = this;

      return {
        total: roles.total,
        data: roles.list
      };
    },
    rolePaginationProps() {
      const total = this.roleTableProps.total || 0;

      return {
        current: this.rolePage,
        pageSize: this.rolePageSize,
        total,
        layout: 'range,total,pager,jumper,jumpbtn'
      };
    },
    isRoleTableLoading() {
      return checkLoading(this.roles.status);
    },
    userTableProps() {
      const {
        users
      } = this;

      return {
        total: users.total,
        data: users.list
      };
    },
    userPaginationProps() {
      const total = this.userTableProps.total || 0;

      return {
        current: this.userPage,
        pageSize: this.userPageSize,
        total,
        layout: 'range,total,pager,jumper,jumpbtn'
      };
    },
    isUserTableLoading() {
      return checkLoading(this.users.status);
    }
  },

  watch: {
    role(val) {
      if (val) {
        this.userPage = 1;
        this.queryUser();
      }
    }
  },

  mounted() {
    this.getHomeRoleSelect().then(res => {
      const json = res && res.length ? res[0] : {};
      this.role = json.roleId;
    });
    this.queryRole(true);
  },

  methods: {
    ...mapActions('home', [
      'getHomeRoleSelect',
      'getHomeRole',
      'getHomeUser'
    ]),

    queryRole(isFirst) {
      const {
        rolePage,
        rolePageSize
      } = this;
      this.getHomeRole({
        offset: (rolePage - 1) * rolePageSize,
        limit: rolePageSize
      });
    },

    queryUser() {
      const {
        userPage,
        userPageSize,
        role
      } = this;
      this.getHomeUser({
        id: role,
        param: {
          offset: (userPage - 1) * userPageSize,
          limit: userPageSize
        }
      });
    },

    handleRolePageChange(page) {
      this.rolePage = page;
      this.queryRole();
    },

    handleRolePageSizeChange(pageSize) {
      this.rolePage = 1;
      this.rolePageSize = pageSize;
      this.queryRole();
    },

    handleUserPageChange(page) {
      this.userPage = page;
      this.queryUser();
    },

    handleUserPageSizeChange(pageSize) {
      this.userPage = 1;
      this.userPageSize = pageSize;
      this.queryUser();
    }
  }
};
</script>




