<template>
  <div class="system-table">
    <data-table
      :loading="isTableLoading"
      :table-props="tableProps"
      :pagination-props="paginationProps"
      @page-change="handlePageChange"
      @page-size-change="handlePageSizeChange"
    >
      <el-table-column
        label="序号"
        type="index"
      />
      <el-table-column
        label="系统"
        prop="name"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <a
            title="查看贴源层子系统详情"
            href="javascript:void(0);"
            @click="showModal(scope.row)"
          >
            {{ scope.row.name }}
          </a>
        </template>
      </el-table-column>
      <el-table-column
        label="逻辑库"
        prop="logicDBTotal"
        align="right"
      >
        <template slot-scope="scope">
          <a
            title="查看贴源层子系统详情"
            href="javascript:void(0);"
            @click="showModal(scope.row)"
          >
            {{ scope.row.logicDBTotal }}
          </a>
        </template>
      </el-table-column>
      <el-table-column
        label="实体库"
        prop="entityDBTotal"
        align="right"
      />
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <gs-button
            type="text-primary"
            @click="showModal(scope.row)"
          >详情</gs-button>
        </template>
      </el-table-column>
    </data-table>

    <gs-modal
      v-model="visible"
      :show-confirm="false"
      :title="activeSystem.name"
      top="80px"
      cancel-text="关闭"
    >
      <data-table
        :loading="isLibraryLoading"
        :table-props="libTableProps"
        :pagination-props="libPaginationProps"
        @page-change="handleLibPageChange"
      >
        <el-table-column
          label="序号"
          type="index"
        />
        <el-table-column
          :label="`逻辑库(总计${activeSystem.logicDBTotal})`"
          prop="name"
          align="right"
        />
        <el-table-column
          :label="`实体库(总计${activeSystem.entityDBTotal})`"
          prop="entityDBTotal"
          align="right"
        />
      </data-table>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import {
  checkLoading
} from 'src/store/status';
// import './style';

export default {
  components: {
    DataTable
  },

  data() {
    return {
      page: 1,
      pageSize: 10,
      libPage: 1,
      libPageSize: 10,
      systemId: '',
      activeSystem: {},
      visible: false
    };
  },

  computed: {
    ...mapState('home', {
      categoryTable: 'categoryTable',
      categoryLibrary: 'categoryLibrary'
    }),

    tableProps() {
      const {
        categoryTable
      } = this;

      return {
        total: categoryTable.total,
        data: categoryTable.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total,
        layout: 'range,total,pager,jumper,jumpbtn'
      };
    },
    isTableLoading() {
      return checkLoading(this.categoryTable.status);
    },
    libTableProps() {
      const {
        categoryLibrary
      } = this;

      return {
        total: categoryLibrary.total,
        data: categoryLibrary.list
      };
    },
    libPaginationProps() {
      const total = this.libTableProps.total || 0;

      return {
        current: this.libPage,
        pageSize: this.libPageSize,
        total,
        layout: 'range,total,pager,jumper,jumpbtn'
      };
    },
    isLibraryLoading() {
      return checkLoading(this.categoryLibrary.status);
    }
  },

  watch: {
  },

  mounted() {
    this.queryCategoryTable();
  },

  methods: {
    ...mapActions('home', [
      'getCategoryTable',
      'getCategoryLibrary'
    ]),

    queryCategoryTable() {
      const {
        page,
        pageSize
      } = this;
      this.getCategoryTable({
        offset: (page - 1) * pageSize,
        limit: pageSize
      });
    },

    queryCategoryLibrary() {
      const {
        libPage,
        libPageSize,
        systemId
      } = this;
      this.getCategoryLibrary({
        id: systemId,
        param: {
          offset: (libPage - 1) * libPageSize,
          limit: libPageSize
        }
      });
    },

    handlePageChange(page) {
      this.page = page;
      this.queryCategoryTable();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.queryCategoryTable();
    },

    handleLibPageChange(page) {
      this.libPage = page;
      this.queryCategoryLibrary();
    },

    showModal(row) {
      this.visible = true;
      this.activeSystem = row;
      this.systemId = row.guid;
      this.libPage = 1;
      this.queryCategoryLibrary();
    }
  }
};
</script>




