import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';
import ECharts from 'vue-echarts';

export default {
  data() {
    return {
      linesColor: ['#007BEC', '#FFD800', '#FF4996']
    };
  },

  computed: {
    ...mapState('home', {
      dbChart: 'dbChart',
      tbChart: 'tbChart',
      techBusiness: 'techBusiness',
      activeBusiness: 'activeBusiness',
      totalBusinessChart: 'totalBusinessChart',
      activeBusinessChart: 'activeBusinessChart'
    }),
    dbChartLoading() {
      return checkLoading(this.dbChart.status);
    },
    tbChartLoading() {
      return checkLoading(this.tbChart.status);
    },
    techBusinessLoading() {
      return checkLoading(this.techBusiness.status);
    },
    activeBusinessLoading() {
      return checkLoading(this.activeBusiness.status);
    },
    totalBusinessChartLoading() {
      return checkLoading(this.totalBusinessChart.status);
    },
    activeBusinessChartLoading() {
      return checkLoading(this.activeBusinessChart.status);
    },
    dbChartOption() {
      const {
        dbChart
      } = this;
      return this.formatBarOption(dbChart);
    },
    tbChartOption() {
      const {
        tbChart
      } = this;
      return this.formatLineOption(tbChart);
    },
    techBusinessOption() {
      const {
        techBusiness
      } = this;
      return this.formatPieOption(techBusiness);
    },
    activeBusinessOption() {
      // const {
      //   activeBusiness
      // } = this;
      const activeBusiness = {
        list: [
          {
            name: '待补充技术信息',
            value: 1200
          },
          {
            name: '已补充技术信息',
            value: 1000
          }
        ]
      };
      return this.formatPieOption(activeBusiness);
    },
    totalBusinessChartOption() {
      const {
        totalBusinessChart
      } = this;
      return this.formatLineOption(totalBusinessChart);
    }
  },

  watch: {
  },

  mounted() {
    this.getDbChart();
    this.getTableChart();
    this.getTechBusiness();
    this.getTotalBusinessChart();
  },

  methods: {
    ...mapActions('home', [
      'getDbChart',
      'getTableChart',
      'getTechBusiness',
      'getActiveBusiness',
      'getTotalBusinessChart',
      'getActiveBusinessChart'
    ]),
    formatBarOption(chart) {
      const names = chart.data.name || [];
      const datas = chart.data.data || [];
      const series = [
        {
          name: names[0],
          type: 'bar',
          barWidth: 10,
          itemStyle: {
            normal: {
              color: new ECharts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  {offset: 0, color: '#4E74FD'},
                  {offset: 1, color: '#1DBDEB'}
                ]
              ),
              barBorderRadius: 5
            }
          },
          data: datas[0]
        }
      ];
      return {
        color: this.linesColor[0],
        legend: {
          data: [
            {
              name: names[0],
              icon: 'circle'
            }
          ],
          right: '10%'
        },
        xAxis: {
          data: chart.data.date || []
        },
        series
      };
    },
    formatLineOption(chart) {
      const names = chart.data.name || [];
      const datas = chart.data.data || [];
      let [series, color] = [[], []];
      const isSingle = names.length === 1;
      names.map((item, i) => {
        series.push({
          data: datas[i],
          name: item,
          type: 'line',
          showSymbol: false,
          symbolSize: 10,
          lineStyle: {
            color: this.linesColor[i],
            shadowBlur: isSingle ? 6 : 0,
            shadowColor: isSingle ? 'rgba(57,83,255,0.60)' : '',
            shadowOffsetY: isSingle ? 3 : 0
          },
          areaStyle: chart.isArea ? {color: '#a3ddff'} : null
        });
        color.push(this.linesColor[i]);
      });
      return {
        color,
        legend: {
          data: names.map(item => { return {name: item, icon: 'circle'}; }),
          right: '10%'
        },
        xAxis: {
          data: chart.data.date || []
        },
        series
      };
    },
    formatPieOption(chart) {
      const list = chart.list || [];
      let total = 0;
      const data = list.map(item => {
        item.label = {
          normal: {
            formatter: '{b|{b}} \n {c|{c}(个)} \n {d|占比{d}%}',
            color: '#AFB1B2',
            rich: {
              b: {
                color: '#AFB1B2',
                padding: [4, 0]
              },
              c: {
                color: '#AFB1B2',
                padding: [2, 0]
              },
              d: {
                color: '#626669',
                fontWeight: 'bold',
                padding: [4, 0]
              }
            }
          }
        };
        total += item.value;
        return item;
      });
      console.log(total);
      return {
        title: {
          text: `${total}`,
          subtext: `总量(个)`,
          subtextStyle: {
            align: 'center',
            width: 100
          },
          left: 'center',
          top: 'middle'
        },
        color: ['#007BEC ', '#01D0CE'],
        series: [
          {
            type: 'pie',
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data
          }
        ]
      };
    }
  }
};



